import {Pipe, PipeTransform} from '@angular/core';

@Pipe({name: 'myTitleBreaker'})

export class BreakerPipe implements PipeTransform {
    transform(value: string): string {
        let parts = value.split(' ');
        if (parts.length > 1) {
            return parts[0] + '<br>' + parts[1];
        } else {
            return parts[0] + '<br>'
        }

    }
}
